// LAYOUT ==================
$god-space: 2%; // the horizontal padding in all the prongs.
$heaven-width: 30%; // prong width right (as a percentage to help scaling).
$hell-width: 30%; // prong width left (as a percentage to help scaling).
$personal-space: 12px; // the vertical padding between believers.
$transition: 0.5s;
// MEDIA BREAKS ==================
$god-loose: 1300px; // loose - prongs divided - no overlap.
$god-cosy: 1000px; // cosy - overlap between sinners and the pillar.
$god-tight: 800px; // tight - believers full width - piller width small margin.
// COLORS ==================
$heaven-color: black;
$pillar-color: black;
$hell-color: #ead1dc;
$god-color: $pillar-color;
$god-blushed: lighten($heaven-color, 45);
// TEXT ==================
$god-h1: 2rem; // font size of h1.
$god-h6: 1rem; // font size of h6.
$god-spoke: $god-h6 * 1.3; // standout with big text.
$god-head: 'Montserrat'; // for heading level text.
$god-body: 'Roboto'; // for body level text.
// BELIEVERS ==================
$container: (main); // sin classes only affect direct children of these
$pillar: (
  h1,
  h2,
  h3,
  h4,
  p,
  nav,
  ol,
  ul,
  dl,
  footer,
  form,
  pre,
  table
); // pillar - all tags are stictly contained the same way - no believers here.
$heaven: (div); // hangs right - gets the best views.
$hell: (blockquote, header, figure); // hangs left.
