menu {
  margin: 0 0 0 -1.5em;
  padding: 0;
  position: fixed;
  left: 50%;
  top: -1.5em !important;
}

menu > label > img {
  border-radius: 50%;
  cursor: pointer;
  display: block;
  width: 3em;
  height: 3em;
  transition: all 0.5s ease-out;
}

menu:hover > label > img {
  transform: scale3d(6.6, 6.6, 1);
  transition-timing-function: cubic-bezier(0.68, 1.55, 0.265, 1);
}

menu > nav {
  padding: 0;
  margin: 0;
}

menu > nav > a {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  transition: transform 0.5s ease;
  z-index: 2;
}

menu > nav > a > img {
  height: 3em;
  width: 3em;
  opacity: 0;
}

menu > nav > a > img:hover {
  transform: scale3d(1.3, 1.3, 1);
  transition-timing-function: cubic-bezier(0.68, 1.55, 0.265, 1);
}

menu:hover > nav > a > img {
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

menu:hover > nav > a:nth-child(1) {
  transform: translate3d(7.82518em, 1.66329em, 0);
}

menu:hover > nav > a:nth-child(1) img {
  opacity: 1;
}

menu:hover > nav > a:nth-child(2) {
  transform: translate3d(3.44409em, 7.22068em, 0);
}

menu:hover > nav > a:nth-child(2) img {
  opacity: 1;
}

menu:hover > nav > a:nth-child(3) {
  transform: translate3d(-3.63192em, 7.12805em, 0);
}

menu:hover > nav > a:nth-child(3) img {
  opacity: 1;
}

menu:hover > nav > a:nth-child(4) {
  transform: translate3d(-7.86604em, 1.45788em, 0);
}

menu:hover > nav > a:nth-child(4) img {
  opacity: 1;
}

